<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      F5data: {
        allBonus: 0,
        balanceBonus: 0,
        vipLength: 0,
      },
      //   currency: "",
        link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    live(num) {
      switch (num) {
        case '0':
          return '--';
        case '4':
          return 'ID';
        case '3':
          return 'OG';
        case '2':
          return 'MD';
        case '1':
          return 'FANS';
          
      }
    },
      // 复制成功时的回调函数
      onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      console.log(hash);
      var url = window.location.origin + window.location.pathname + "#/home/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
         protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(34, 42)
        : addr;
    },
    //领取分红奖励
    getvipDividendFun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .getBonusReward()
        .send({ from: account ,value: '5000000000000000'})
        .then((res) => {
          console.log("getvipDividendFun", res);
          // if (res.status) {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          // _this.getCoinBalance();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          // this.getMiningInfoFun(1);
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  // beforeCreate(){
  //   var h = window.location.hash;
  //   console.log(window)
  //   window.location.href='https://flarenft.github.io/'+h;
  // },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
