    }
  }

  .cent {
    width: 92%;
    margin: 30px auto;
    padding: 0 0 50px;
    position: relative;
    // background: rgba(15,20,32,.5);
    border-radius: 15px;
  }

}
  .btn{
    width: 100%;
  margin: 0 auto 20px;
  display: block;
  background: #0627FF;
  border: none;
  text-align: center;
