  }

    .my {
      //      background: url(../../assets/img/fan/1.png) top center no-repeat;
      // background-size:100% ;
      margin: 0 auto 20px;
      padding: 12px 25px 0;

      .box {
        background: rgba(32, 41, 92, .7);
        border-radius: 10px;
        margin: 5px 0;
        padding: 15px 10px;
        text-align: center;

        span {
          color: #fff;

          img {
            margin-right: 5px;
            vertical-align: middle;
          }
        }

        h3 {
          text-align: center;
          color: #26DDFE;
          margin: 8px 0 0 0;
        }
      }
    }

    .list {
              background: url(../../assets/img/new/12.png) top center no-repeat #000;
      background-size:100% 100%;
      padding: 5px 20px;
        margin: 20px auto 20px;
        box-sizing: border-box;

        .line {
          background: #1F232F;
          font-size: 16px;
          margin: 20px 0;
          padding: 15px 20px;
          border-radius: 15px;
        }

        span {
          display: inline-block;
          padding: 6px 0;
          color: #fff;
        }

        .textRight span {
          font-size: 16px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 600;
          color: #FCD110;
          overflow: hidden;
          display: block;
          width: 100%;
          text-align: right;
          text-overflow: ellipsis;
        }

      }
</style>



