            <van-row type="flex" justify="space-between" gutter="10">
              <van-col span="12">
                <div>
                  <span> <img src="../../assets/img/new/4.png" alt="" width="20"> {{ $t("message.f_29") }}</span>
                  <h3 class="box">{{ hmdInfo ? hmdInfo.info[2] : "0" | figure }} USDT</h3>
                </div>

              </van-col>
              <van-col span="12">
