                <div>
                  <span><img src="../../assets/img/new/7.png" alt="" width="20"> {{ $t("message.f_28") }}</span>
                  <h3 class="box">{{ hmdInfo ? (Number(hmdInfo.info[18])) : "0" | figure }} Flare</h3>
                </div>

              </van-col>
            </van-row>
          </div>

          <div class="list">
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_25") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[1] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_30") }}</span></van-col>
              <van-col span="12" class="textRight"><span> {{ hmdInfo ? live(hmdInfo.info[7]) : "--" }}</span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_31") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[12] : "0" }} </span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_32") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[4] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>全球累计分红收益</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[34] : "0"  | figure }} USDT</span></van-col
              >
            </van-row> -->
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_33") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[5] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <!-- <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>{{ $t("message.f_34") }}</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[34] : "0" | figure }} USDT</span>
            </van-col>
          </van-row> -->
            <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>投资笔数</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[13] : "0" }} </span></van-col>
            </van-row> -->
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_35") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[17] : "0" | figure }} Flare</span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_36") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{
                  protectAddress('0x85AA3f04e539e426cbB55c0D584ea99cFE1D96A1', 4)
              }}</span></van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_37") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ link }}</span></van-col>
            </van-row>
          </div>
          <button class="btn" v-clipboard:copy="link" v-clipboard:success="onCopy" v-clipboard:error="onError">
            <img src="../../assets/img/new/10.png" alt="" width="16">
          {{$t("message.f_21")
          }}
          </button>
        </div>
      </div>


    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account",]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    live(num) {
      switch (num) {
        case '0':
          return '--';
        case '4':
          return 'ID';
        case '3':
          return 'OG';
        case '2':
          return 'MD';
        case '1':
          return 'FANS';

      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getUsdt(num) {
      return num * this.hmdInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      console.log(hash);
      var url = window.location.origin + window.location.pathname + "#/home/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    getPrice() {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      this.mxtContract.methods
        .getTokenPrice(
          "0x0496c8f51b6e6500bdd6e3bc0664857e976c6bc6",
          "0x6e42eae3317a19eb4b5115b0bce388bbf21cb917",
          "1000000000000000000"
        )
        .call()
        .then((res) => {
          // console.log('shdjsh')
          this.TokenPrice = parseFloat(
            web3.utils.fromWei(res, "ether")
          ).toFixed(4);
          console.log("getTokenPrice======", web3.utils.fromWei(res, "ether"));
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
          console.log("withdrawProfit", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },



  },
  // beforeCreate(){
  //   var h = window.location.hash;
  //   console.log(window)
  //   window.location.href='https://flarenft.github.io/'+h;
  // },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      // this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          _this.dataInfo = _this.hmdInfo.info;
          // _this.getPrice();
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 10000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
