          padding: 15px 20px;
          border-radius: 15px;
        }

        span {
          display: inline-block;
          padding: 6px 0;
          color: #fff;
        }

        .textRight span {
          font-size: 16px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 600;
          color: #FCD110;
          overflow: hidden;
          display: block;
          width: 100%;
          text-align: right;
          text-overflow: ellipsis;
        }

      }
    }

    .my {
      margin: 0 auto 20px;
      padding: 46px 20px;
      border-radius: 10px;
      position: relative;
       background: url(../../assets/img/new/11.png) top center no-repeat #000;
      background-size: 100%;

      .box {
        background: #1F232F;
        border-radius: 10px;
        margin: 15px 0 0;
        padding: 20px 10px;
        text-align: left;
        font-size: 16px;
      }

      span {
        color: #fff;
        font-size: 16px;
        margin-top: 22px;
        display: block;

        img {
          margin-right: 5px;
          vertical-align: sub;
        }
      }

      h3 {
        text-align: left;
        color: #FCD110;
        margin: 8px 0 0 0;
      }
    }

    .ming_bix::before {
      content: "";
      display: block;
      width: 200px;
      height: 200px;
      background: url(../../assets/img/new/1.png) top center no-repeat;
      background-size: 100% 100%;
      position: absolute;
      top: -40px;
      right: -30px;
    }

    .ming_bix {
      background: url(../../assets/img/new/info_bg.png) top center no-repeat #000;
      background-size: 100% 100%;
      width: 90%;
      margin: 0 auto;
      padding: 20px 40px;
      box-sizing: border-box;
      position: relative;

      .cent_line {
        height: 1px;
        border-bottom: 2px dashed #F506FF;
      }

      span {
        color: #ccc;
        display: block;
        margin-top: 25px;
      }

      h3 {
        font-family: DINPro;
        font-weight: bold;
        color: #fff;
        font-size: 26px;
        line-height: 24px;
        margin-top: 10px;
        margin-bottom: 25px;

        small {
          color: #ccc;
          font-size: 15px;
          margin-top: 5px;
        }
      }

    }
  }
}

.my::before {
  content: "";
  display: block;
  width: 80px;
  height: 80px;
  background: url(../../assets/img/new/9.png) top center no-repeat;
  background-size: 100% 100%;
  position: absolute;
  bottom: -30px;
  right: -20px;
}

.img_tiele {
  width: 90%;
  margin: 150px auto 0;
  position: relative;

  .ren {
    position: absolute;
    right: -20px;
    top: -100px;
    z-index: 2;
  }
}

.btn {
  width: 100%;
  margin: 0 auto 20px;
  display: block;
  background: #0627FF;
  border: none;
  text-align: center;
  color: #fff;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
