<template>
  <header>
    <div id="header">
      <van-row align="center" justify="space-between" type="flex">
        <van-col span="6">
          <a v-if="isHome" @click="hreftwo('/home')">
            <img
              style="width: 80px;"
              src="../assets/img/fan/logo.png"
            /> </a
        >
          <a v-else @click="hreftwo('/home')">
            <img
              style="width:40px; "
              src="../assets/img/fan/bank.png"
            /> </a
        >
        </van-col>
        <van-col span="12" >
              <div class="bgWhite flex" v-if="isHome" style="padding:9px 5px">
                <div class="bg_logo">
                  <img src="../assets/img/topLogo.png" alt="" width="100%"/>
                  <span>BSC</span>
                </div>
                <p>{{ protectAddress(this.account, 4) }}</p>
              </div>
              <div v-else class="name">{{name}}</div>
        </van-col>
        <van-col span="6"  @click="showPopup" class="textRight"    style="margin-top: 5px;"><img src="../assets/img/fan/mune.png" alt="" width="40"/></van-col>
      </van-row>

      <van-popup
        v-model="show"
        position="right"
        :style="{ height: '100%', width: '65%' ,background: 'linear-gradient(104deg,#362F6F, #1F232F,#1F232F)'}"
      >
        <div class="menu">
          <img src="../assets/img/fan/logo.png" alt="" width="55%" style="margin-bottom:20px"/>
           <div class="bgWhite flex" >
                <div class="bg_logo">
                  <img src="../assets/img/topLogo.png" alt="" />
                  <span>BSC</span>
                </div>
                <p>{{ protectAddress(this.account, 4) }}</p>
              </div>
          <div class="list">
            <div class="li_col">
              <img src="../assets/img/mune/icon1.png" alt="" width="20" />
              <a  @click="hreftwo('/home')">{{ $t("message.m_1") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/mune/icon2.png" alt="" width="20" />
              <a @click="hreftwo('/info')">{{ $t("message.m_2") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/mune/icon3.png" alt="" width="20" />
              <a @click="hreftwo('/ming')">{{ $t("message.m_3") }}</a>
            </div>
            <!-- <div class="li_col">
              <img src="../assets/img/mune/icon9.png" alt="" width="20" />
              <a @click="hreftwo('/nft')">{{ $t("message.m_4") }}</a>
            </div> -->
            <div class="li_col">
              <img src="../assets/img/mune/icon4.png" alt="" width="20" />
              <a >{{ $t("message.m_5") }}</a>
              <ul>
                <li :class="lang == 'zhCHS'?'in':''" @click="changeLocale('zhCHS')">{{ $t("message.m_6") }}</li>
                 <li :class="lang == 'en'?'in':''" @click="changeLocale('en')" >{{ $t("message.m_7") }} </li>
                 <div style="clear:both"></div>
              </ul>
            </div>
            <div class="li_col">
              <img src="../assets/img/mune/icon5.png" alt="" width="20" />
              <a href="https://bscscan.com/">{{ $t("message.m_8") }}</a>
            </div>
            <!-- <div class="li_col">
              <img src="../assets/img/mune/icon8.png" alt="" width="20" />
              <a @click="tips">{{ $t("message.f_a_7") }}</a>
            </div> -->
            <div class="li_col">
              <img src="../assets/img/mune/icon7.png" alt="" width="20" />
              <a href="https://metamabsc.github.io/MetaFlareDao.pdf"  target="_blank">{{ $t("message.m_13") }}</a>
            </div>
            <div class="li_col">
              <img src="../assets/img/mune/icon6.png" alt="" width="20" />
              <a href="https://www.metaflare.store">{{ $t("message.m_11") }}</a>
            </div>
          </div>
          <!-- <div class="foot">
            <a href="https://t.me/Alles_global"
              ><img src="../assets/img/mune/1.png" alt="" width="25"
            /></a>
            <a href="https://t.me/Alles_global"
              ><img src="../assets/img/mune/2.png" alt="" width="25"
            /></a>
            <a href="https://discord.gg/y56k8AXjwE"
              ><img src="../assets/img/mune/3.png" alt="" width="25"
            /></a>
            <a href="https://www.facebook.com/AllesGlobal"
              ><img src="../assets/img/mune/4.png" alt="" width="25"
            /></a>
          </div> -->
        </div>
      </van-popup>
    </div>
  </header>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      isShowNav: false,
      showRule: false,
      show: false,
      lang_1:null,
      langList: [
        {
          text: "中文",
          img_txt: "zhCHS",
        },
        {
          text: "EN",
          img_txt: "en",
        },
      ],
    };
  },
  props: {
    lang: {
      type: String,
      default: "zhCHS",
    },
    type: {
      type: String,
      default: "home",
    },
     name: {
      type: String,
      default: "",
    },
    isHome: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("main", ["account", "oktInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_oktInfo"]),
    showPopup() {
      this.show = true;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    changeLocale(locale) {
      localStorage.setItem("lang", locale);
      this.$emit("changeAppLocale", locale);
      // this.isShowNav = !this.isShowNav;
    },
    LanguageClick: function () {
      this.isShowNav = !this.isShowNav;
    },
    tips() {
      this.$toast.fail(this.$t("message.tips3"));
      // this.$dialog.alert({
      //   message: this.$t("message.tips3"),
      //   theme: "round-button",
      // });
    },
    prev() {
      this.$router.go(-1);
    },

    // 	showPopup() {
    //   this.showRule = true;
    // },
    hreftwo(url) {
      // console.log(url.length);
      // if (url.length > 1) {
      //   this.$router.push({ path: url + "/" + this.lang });
      // } else {
        if(url.length>2){
          this.$router.push({ path: url + "/?invite=" + this.$route.query.invite  });
        }else{
          this.$router.push({ path: url });
        }
      
      // }
    },
  },
};
</script>

<style scoped lang="less">
#header {
  padding: 15px 5%;
  // position: fixed;
  // background: #03030D;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  z-index:10;
}
  .name{
    color: #fff;
    font-size: 17px;
    text-align: center;
  }
.bgWhite {
  padding:11px 5px;
  background: url(../assets/img/mune/bsc_line.png) top center no-repeat;
    background-size: 100%;
    font-size: 18px;
  p {
    margin-left: 10px;
    height: 25px;
    line-height: 25px;
    color: #fff;
  }
}
.flex {
  display: flex;
}
.bg_logo {
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 600;
  color: #fff;
  // background:#333333;
  border-radius: 10px;
  padding: 0 6px 0 5px;
    background: #333;
  span{display:inline-block;vertical-align: text-bottom;}
  img {
    width: 18px;
    vertical-align: unset;
  }
}
.menu {
  padding:50px 35px;
  position: relative;
  height: 90%;
  text-align: left;
  .list {
    // border-top: 2px solid #182034;
    margin-top: 50px;
    padding-top: 10px;
    img {
      vertical-align: bottom;
      float: left;
      margin-top:20px;
    }
    a {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 500;
      color: #fff;
      display: block;
      padding: 18px 20px 18px 30px
    }
  }
  ul{
    width: 150px;
    background: #333;
    border-radius: 24px;
    opacity: 1;
    margin: 10px 0 20px 30px;
  }
   .li_col li.in{
    background: #664AF5;
   }
  .li_col li{
    padding: 10px 10px;
    color: #fff;
    float: left;
    width: 50%;
    box-sizing: border-box;
    border-radius: 24px;
    text-align: center;
    // .gou{
    //       width: 20px;
    // float: none;
    // margin-top: 0;
    // margin-left: 20px;
    // }
  }

  .foot {
    position: absolute;
    bottom: 20px;
    img {
      display: inline-block;
      margin-right: 8px;
    }
  }
}
@media (max-width:390px) {
  .bgWhite{
    font-size: 14px;
    padding: 8px 5px;
  }
}
</style>



