  .langs {
    width: 80px;
    text-align: center;
    position: absolute;
    right: -5px;
    border: 1px solid #1db6f4;
    padding: 10px;
    border-radius: 10px;
    top: 100px;

    li {
      text-align: center;
    }
  }
}

.top {
  background: url(../../assets/img/top-left.png) no-repeat center left;
  background-size: 40%;

  .text {
    margin-top: 120px;

    h1 {
      font-size: 60px;
      font-family: AlibabaSans-Heavy, AlibabaSans;
