  }

  p {
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    line-height: 24px;
    margin-top: 30px;
  }
}

.card {
  background: url(../../assets/img/card.png) no-repeat right bottom;
  background-size: 200px;
  padding: 0px 0px 100px;

  img {
    vertical-align: bottom;
    width: 50px;
  }

  h2 {
