  }

  span {
    font-size: 16px;
    display: inline-block;
    line-height: 50px;
  }
}

.swipe {
  margin: 80px 0;
}

.footer {
  background: url(../../assets/img/boot.png) no-repeat center bottom;
  background-size: 100%;

  .fot {
    padding-top: 50px;
  }

  padding-bottom: 100px;

  a {
    display: inline-block;
    margin: 0 10px;
  }

  .map {
    padding: 50px 0;
    margin-bottom: 120px;

    .title {
      margin-bottom: 80px;
    }
  }

  .partner {
    position: relative;

    .list {
      padding: 60px 0;
    }

    img {
      margin: 10px 0;
    }
  }

  // .partner::before {
  //   display: block;
  //   content: '';
  //   width: 250px;
  //   height: 180px;
  //   position: absolute;
  //   background: url(../../assets/img/uni.png) no-repeat center;
  //   background-size: 100%;
  //   right: 0px;
  //   top: -180px;
  // }
}

.cent {
  position: relative;
  background: url(../../assets/img/cent-bg.png) no-repeat center;
  background-size: 100%;

  .videoBox {
    padding: 120px 0;

    .sp {
      width: 80%;
      height: 540px;
      margin: 80px auto 50px;
    }
  }

  p {
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    line-height: 24px;
    margin-top: 30px;
  }

  .top-m {
    margin-top: 80px;
  }

  .about {
    margin-bottom: 100px;
  }
}

.cent::before {
  display: block;
  content: '';
  width: 250px;
  height: 580px;
  position: absolute;
  background: url(../../assets/img/bg-x.png) no-repeat center;
  background-size: 100%;
  right: 0px;
  top: -180px;
}
.put {
  font-size: 16px;
  margin: 5px 0 10px;
  input{
        font-size: 20px;
        font-family: SourceHanSansCN-Bold, SourceHanSansCN;
        font-weight: bold;
        color: #fff;
        line-height: 30px;
        width: 180px;
        border: none;
        padding: 5px 10px;
        border-radius: 10px;
        background:#202f45;
      }
}

.btnBox {
  div {
    width: 70px;
    height: 70px;
    background: url(../../assets/img/next.png) no-repeat center;
    background-size: 100%;
    display: inline-block;
    margin-left: 30px;
  }

  .prev {
    background: url(../../assets/img/prev.png) no-repeat center;
    background-size: 100%;
  }
}

.btn {
  width: 100%;
  margin: 40px auto;
  text-align: center;
  border: none;
  font-size: 16px;
  padding: 10px 0;
  border-radius: 20px;
  background: linear-gradient(-45deg, #1db6f4, #c13ef8);
}

.mobile_box {
  display: none;
}

.pc_box {
  display: block;
}

@media (max-width: 420px) {

  .header,
  .pc_box {
    display: none;
  }

  .mobile_box {
    display: block;
  }

  .web {
    padding: 0 20px;
  }

  .header2 {
    display: block;
    padding: 20px 0;

    .lang {
      width: 90px;
      background: url(../../assets/img/lang.png) no-repeat;
      background-size: 100%;
      text-align: center;
      font-size: 16px;
      padding: 10px 0;
      display: inline-block;
    }
      .langs {
    width: 80px;
    text-align: center;
