    right: 20px;
    border: 1px solid #1db6f4;
    padding: 10px;
    border-radius: 10px;
    top: 70px;
    background: #04031B;
  z-index: 2;
    li {
      text-align: center;
      font-size: 16px;
      padding: 5px 0;

    }
  }
  }

  .about {
    padding: 50px 0;
  }

  .title {
    font-family: AlibabaPuHuiTi-Bold, AlibabaPuHuiTi;
    font-weight: bold;
    font-size: 20px;
  }

  .title span::before {
    height: 80px;
    width: 30px;
    right: -33px;
  }

  .top {
    .text {
      margin-top: 0;
      margin-bottom: 30px;

      h1 {
        font-size: 40px;
        text-align: center;
      }

      h3 {
        font-size: 16px;
        text-align: center;
      }
    }

    .about_img {
      margin: 30px auto 20px;
    }
  }

  .cent .videoBox .sp {
    width: 100%;
    height: 140px;
    margin: 30px auto 20px;
    z-index: 10;
  }

  .cent .videoBox {
    padding: 120px 0 50px;
  }
.card h2{
    margin: 40px 0 20px;
}
}
</style>



