<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      tabsNum: 1,
      list:null,
      //   currency: "",
      //   link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      //   timer: null,
      //   timer2: null,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "nftContract", "account", "nftInfo"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_nftContract"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
          //查询nft
    getNFTFun: function () {
      
      if (this.nftContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      console.log("getNFTFun", 1);
      var _this = this;
      var account = _this.account;
      // if (vipAvgBonus <= 0) {
      //   this.$toast(this.$t("message.bonusMsg"));
      //   return;
      // }
      // this.$toast.loading({
      //   // message: this.$t("message.pack"),
      //   duration: 0,
      //   overlay: true,
      //   closeOnClickOverlay: true,
      // });
      this.nftContract.methods
        .tokenURI(0)
        .call({ from: account })
        .then((res) => {
          console.log("getNFTFun", res);
          _this.$toast.clear();
         this.list = res
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
       this.add_nftContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.nftContract) {
          getData();
        } else {
           this.getNFTFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  beforeCreate(){
    var h = window.location.hash;
    console.log(window)
    window.location.href='https://flarenft.github.io/'+h;
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

