import { setStorage, getStorage } from '@/assets/js/units.js';
import getWeb3 from '@/assets/js/getWeb3.js'
import OKTABI from '@/assets/coin_abi/DOT.json'
import HMDABI from '@/assets/coin_abi/HMD.json'
import pool_other from '@/assets/pool_abi/pool_other.json'
import pool_NFT from '@/assets/pool_abi/pool_dao.json'//nft卡牌
import pool_buy from '@/assets/pool_abi/buy.json'//nft卡牌
import getNft from '@/assets/pool_abi/getNft.json'//nft卡牌
import Vue from 'vue'


const state = {
  /*存储用户信息全局变量*/
  account: JSON.parse(getStorage('account') || 0),
  balance: JSON.parse(getStorage('balance') || 0),
  hmdBalance: JSON.parse(getStorage('hmdBalance') || 0),
  FiveDead: JSON.parse(getStorage('FiveDead') || 0),
  mxtCoinContract: JSON.parse(getStorage('mxtCoinContract') || null),//质押的usdt
  mxtContract: JSON.parse(getStorage('mxtContract') || null),
  hmdInfo: JSON.parse(getStorage('hmdInfo') || null),
  FiveCoinContract: JSON.parse(getStorage('FiveCoinContract') || null),//five币合约
  nftContract: JSON.parse(getStorage('nftContract') || null),
  nftInfo: JSON.parse(getStorage('nftInfo') || null),//NFT合约
  buyNftContract: JSON.parse(getStorage('buyNftContract') || null),//NFT合约
  getNftContract: JSON.parse(getStorage('getNftContract') || null),//NFT合约
  getNftInfo: JSON.parse(getStorage('getNftInfo') || null),//NFT合约
}

const getters = {
  /*获取用户信息*/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {

  set_account: (state, data) => {
    state.account = data;
  },
 
  set_balance: (state, data) => {
    state.balance = data;
  },
  set_hmdBalance: (state, data) => {
    state.hmdBalance = data;
  },
  // usdt测试币
  set_mxtCoinContract: (state, data) => {
    state.mxtCoinContract = data;
  },
 
  // 矿池
  set_hmdInfo: (state, data) => {
    state.hmdInfo = data;
  },
  set_mxtContract: (state, data) => {
    state.mxtContract = data;
  },
  // NFT
  set_nftContract: (state, data) => {
    state.nftContract = data;
  },
  set_nftInfo:(state, data) => {
    state.nftInfo = data;
  },
  set_FiveCoinContract:(state, data) => {
    state.FiveCoinContract = data;
  },

  //DAO
  set_FiveDead:(state, data) => {
    state.FiveDead = data;
  },
    //DAO
    set_buyNftContract:(state, data) => {
      state.buyNftContract = data;
    },
    set_getNftContract:(state, data) => {
      state.getNftContract = data;
    },
    set_getNftInfo:(state, data) => {
      state.getNftInfo = data;
    },
}

const actions = {

  // 矿池1 HMD
  add_hmdInfo: ({ commit }, data) => {
    var _this = this;
    let info = {};
    getWeb3.Init(pool_other, Vue.prototype.ArrList.dappArr, (contract, addr, balance) => {
      commit('set_mxtContract', contract);
      commit('set_account', addr);
      commit('set_balance', web3.utils.fromWei(balance, 'ether'));
      info.address = Vue.prototype.ArrList.dappArr;
      contract.methods.getUserInfo(addr,1).call().then((res) => {
        // console.log(res)
        info.info = res[0];
        info.inviteCode = res[1];
        info.referrer = res[2];
       
      }).then(()=>{
        // five单价
        contract.methods.getNewTokenPrice( Vue.prototype.ArrList.UsdtArr,Vue.prototype.ArrList.FiveArr,'1000000000000000000').call()
        .then((data1) => {
          var prex =4;
          var price = web3.utils.fromWei(data1, 'ether');
          info.fivePrice =null;
          if (parseInt(price) == price){
            info.fivePrice=price;
         
            return 
          } 
          let len = 1;
          let i = 0;
          while (parseInt(price) != price) {
            price = price * 10;
              len *= 10;
              i++;
          }
          let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
          let d1 = price - last;
          if (last > 0) {
              d1 = d1 + Math.pow(10, (i - prex));
          }
          d1 = d1 / len;
         info.fivePrice=d1;
        //  commit('set_hmdInfo', info);
        //  console.log('set_hmdInfo=============',info)
        }).then(()=>{
          // AES单价
          contract.methods.getNewTokenPrice(Vue.prototype.ArrList.FiveArr,Vue.prototype.ArrList.UsdtArr,'1000000000000000000').call()
          .then((data) => {
            var prex =4;
            var price = web3.utils.fromWei(data, 'ether');
            // console.log(price)
            info.AESPrice =null;
            if (parseInt(price) == price){
              info.AESPrice=price;
              // console.log(info)
                    commit('set_hmdInfo', info);
              return 
            } 
            let len = 1;
            let i = 0;
            while (parseInt(price) != price) {
              price = price * 10;
                len *= 10;
                i++;
            }
            let last = Math.abs(i <= prex ? 0 : price % (Math.pow(10, (i - prex))));
            let d1 = price - last;
            if (last > 0) {
                d1 = d1 + Math.pow(10, (i - prex));
            }
            d1 = d1 / len;
           info.AESPrice=d1;
           commit('set_hmdInfo', info);
           console.log('set_hmdInfo=============',info)
          })
        })
      })
      

    })
  },

  // 获取usdt币合约对象
  add_mxtCoinContract: ({ commit }, data) => {
    getWeb3.Init(OKTABI, Vue.prototype.ArrList.UsdtArr, (contract, addr, balance) => {
      commit('set_mxtCoinContract', contract)
    })
  },
  
  // five币币合约对象
  add_hmdCoinContract: ({ commit }, data) => {
    var info = {};
    getWeb3.Init(HMDABI, Vue.prototype.ArrList.FiveArr, (contract, addr, balance) => {
      commit('set_FiveCoinContract', contract);
      contract.methods
        .balanceOf(addr)
        .call()
        .then((res) => {
          commit('set_hmdBalance', res)
        }).then(()=>{
          contract.methods
            .balanceOf(Vue.prototype.ArrList.XhArr)
            .call()
            .then((res1) => {
              info.res1 = res1;
            })
        }).then(()=>{
          contract.methods
            .balanceOf('0x000000000000000000000000000000000000dEaD')
            .call()
            .then((res2) => {
              info.res2 = res2;
            })
        }).then(()=>{
          contract.methods
            .balanceOf('0x0000000000000000000000000000000000000000')
            .call()
            .then((res3) => {
              info.res3 =res3
            })
        }).then(()=>{
          contract.methods
            .balanceOf('0x181bcC32D5Dc33025f50E108F18543b0DCC23395')
            .call()
            .then((res4) => {
              info.res4 =(Number(res4)+300000000000000000);
              console.log('set_FiveDead=========',info)
              commit('set_FiveDead', info)
            })
        });
    })
  },
   // NFT合约对象
   add_nftContract: ({ commit }, data) => {
    var nft = {};
    getWeb3.Init(pool_NFT, Vue.prototype.ArrList.AesLpArr, (contract, addr, balance) => {
      commit('set_nftContract', contract);
      contract.methods
        .balanceOf(addr)
        .call()
        .then((res) => {
          nft.balanceOf=res;
          // commit('set_hmdBalance', res)
        }).then(()=>{
          contract.methods
            .name()
            .call()
            .then((res1) => {
              nft.name = res1;
            })
        }).then(()=>{
          contract.methods
            .symbol()
            .call()
            .then((res2) => {
              nft.symbol = res2;
            
            }).then(()=>{
              contract.methods
                .totalSupply()
                .call()
                .then((res3) => {
                  nft.totalSupply = res3;
                  console.log('set_nftInfo=========',nft)
                  commit('set_nftInfo', nft)
                })
            })
        })
    })
  },
   // 购买nft
   add_buyNftContract: ({ commit }, data) => {
    getWeb3.Init(pool_buy, Vue.prototype.ArrList.BuyArr, (contract, addr, balance) => {
      commit('set_account', addr);
      commit('set_buyNftContract', contract)
    })
  },
    // 获取nft
    add_getNftContract: ({ commit }, data) => {
      getWeb3.Init(getNft, '0x528944971817ed23b4bd2830a0d92b7ff89ed56d', (contract, addr, balance) => {
        commit('set_getNftContract', contract)
        contract.methods
        .getInfo(addr)
        .call()
        .then((res) => {
          commit('set_getNftInfo', res)
          console.log('set_getNftInfo',res)
        })
      })
    },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}




